.background_img{
    position: absolute;
    width: 100%;
    height: 320px;
    z-index: 0;

    opacity: 0.6;
}

.prod_exp {
    position: absolute;
    top: 140px;
    left: 170px;

    color: #222;
    text-shadow: 1px 1px 5px #fff;
}
.sub_broad{
    position: relative;
    left: 5px;
}

.sub_inimg{
    width: 130px;
    position: absolute;

    z-index: 10;

    top: 130px;
    left: 30px;

    box-shadow: 5px 5px 5px #999;
    border-radius: 5px;
}

.drama_exp {
    position: relative;

    top: 350px;

    width: 500px;

}
.drama_exp>p{
    display: inline-block;
    width: 400px;

    margin-left: 50px;

    text-shadow: 3px 3px 5px #9d9d9d;

    font-size: 1.05em;
    font-weight: 500;
    color: #555;
}
.sub_href{
    display: inline-block;
    padding: 5px 10px;

    margin-top: 65px;

    font-weight: bold;
    text-align: center;

    text-shadow: none;
    
    border-radius: 15px;

    background-color: #ccc;

    border: 1px solid #9d9d9d;

    cursor: pointer;
    transition: all 0.2s;
}
.sub_href:hover{
    background-color: #79b4b7;
    color: #fefbf3;
    transition: all 0.2s;
}

.exitBtn{
    font-weight: bold;
    font-size: 2em;
    position: absolute;
    right: 15px;
    top: 12px;
    width: 40px;
    height: 40px;

    line-height: 28px;

    text-align: center;

    cursor: pointer;

    z-index: 10;

    border-radius: 50%;
    border: 1px solid #fff;
    background-color: #ccc;

    color: #666;
    transition: all 0.1s;
}

.exitBtn:hover{
    background-color: #79b4b7;
    color: #fff;
    transition: all 0.1s;
}

