
nav {
    width: 576px;
    margin: 0 auto;
    text-align: center;
    align-items: center;

    background-color: #fefbf3;
}
nav>ul {
    height: 50px;
    width: 550px;
    margin: 0 auto;
}
.DoW{
    list-style: none;
    display: inline-block;
}
.DoW >a{
    text-decoration: none;
    color: #333;
    font-size: 1.3em;
    display: inline-block;
    
    width: 70px;
    height: 70px;
    background-color: #ccc;

    border-right: 1px solid #666;
    

    border-radius: 0 0 10px 10px;

    /* float: left; */
    margin: 0 1px 0 1px;

    text-align: center;
    line-height: 70px;

    transition: all, 0.3s;
    
    font-weight: bold;
    position: relative;
    top: -25px;

    box-shadow: 3px 3px 5px #ccc;
    
}

.DoW .active {
    background-color: #79b4b7;
    color: #fff;
    transition: all, 0.3s;
    top: 0;
}

nav{
    width: 576px;
    margin: 0 auto;
}
.dramaList {
    height: 150px;
    
    background-color: #f8f0df94;
    list-style: none;
    border: 1px solid #ccc;
    border-radius: 30px;
    margin:25px 0;

    position: relative;

    cursor: pointer;

    box-shadow: 0px 3px 5px #ccc;
    transition: all 0.2s;

    text-shadow: 2px 2px 5px #9d9d9d;
}
.dramaList:hover{
    transform: translate(-10px, -10px);
    box-shadow: 7px 7px 5px #ccc;
    transition: all 0.2s;
}
.dramaList > p{
    display: flex;
    flex-direction: row;
}
.drama_Img {
    flex:2 2 0;
    position: relative;
}
.drama_Sub{
    flex:3 3 0;
    text-align: center;
}

.drama_Sub>h2{
    margin-top: 30px;
}
.dramaImg {
    height: 130px;
    position: relative;

    left:20px;
    top: 10px;

    box-shadow: 5px 5px 5px #ccc;

    border-radius: 10px;
}